<!--  -->
<template>
  <div class="main">
    <div class="bg_mask"></div>
    <div class="bg" :style="{backgroundImage: 'url('+songInfo.pic+')'}"></div>
    <div class="main_con">
      <!-- 列表 -->
      <div>
        <div>
          <div class="info">
            <div class="cover_out">
              <img class="pic" :src="songInfo.pic||'noimg.jpg'" @error="imgError($event)" />
            </div>
            <div>
              <span class="title">{{songInfo.name}}</span>
              <p class="artist flex_c">
                <span>{{songInfo.artist}}</span>
              </p>
              <p class="song_info">
                <span>专辑：</span>
                <span class="album_name">{{songInfo.album}}</span>
                <span>发行时间：</span>
                <span class="time">{{songInfo.releaseDate}}</span>
              </p>
            </div>
          </div>
        </div>
        <div class="lrc" ref="lrc">
          <p v-for="i in 4" :key="'before'+i"></p>
          <p v-for="(item,index) in lrcList" :key="index" :class="{active:isLrc===index}">{{item.lineLyric}}</p>
          <p v-for="i in 4" :key="'after'+i"></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { HttpManager } from "../../api";
import MusicPlay from '../../components/MusicPlay.vue'
import MusicNoData from "../../components/MusicNoData.vue";
import { mapGetters } from 'vuex';
export default {
  name: "SongDetail",
  data () {
    return {
      code: 0,
      status: 0,
    };
  },

  components: {
    MusicNoData,
    MusicPlay
    // MusicTable,
    // Pagination
  },

  methods: {
    getSongInfo () {
      let that = this;
      HttpManager.getSongInfo(this.$route.params.rid)
        .then((res) => {
          that.code = parseInt(res.code);
          that.$store.commit('setSongInfo', res.data);
        })
        .catch(() => { });
      HttpManager.getSongLrc(this.$route.params.rid)
        .then((res) => {
          that.status = parseInt(res.status);
          that.$store.commit('setLrcList', res.data.lrclist);
        })
        .catch(() => { });
    },
    getSongUrl () {
      let that = this;
      HttpManager.getPlayUrl({ mid: this.$route.params.rid, type: 'music' })
        .then((res) => {
          // that.status = parseInt(res.status);
          that.$store.commit('setSongUrl', res.data.url);
        })
        .catch(() => { });
    },
    imgError (event) {
      event.currentTarget.src = "https://h5static.kuwo.cn/upload/image/4f768883f75b17a426c95b93692d98bec7d3ee9240f77f5ea68fc63870fdb050.png";
      event.currentTarget.onerror = null;
    }

  },

  computed: {
    ...mapGetters([
      'isLrc',
      'lrcList',
      'curTime',
      'songInfo',
    ])
  },
  watch: {
    isLrc: {
      handler (newVal, oldVal) {
        let lrcContent = this.$refs.lrc;
        // if (newVal > oldVal) {
        lrcContent.scrollTop = newVal * 40;
        // }
      },
      // immediate: true, //监听首次绑定值
    },
    curTime: {
      handler (newVal, oldVal) {
        for (let i = 0; i < this.lrcList.length - 1; i++) {
          if (parseFloat(this.lrcList[i].time) <= newVal + 0.4 && parseFloat(this.lrcList[i + 1].time) >= newVal + 0.4) {
            this.$store.commit('setIsLrc', i);
          }
        }
        if (parseFloat(this.lrcList[this.lrcList.length - 1].time) <= newVal + 0.4) {
          this.$store.commit('setIsLrc', this.lrcList.length - 1);
        }
      },
      // immediate: true, //监听首次绑定值
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getSongInfo();
    this.getSongUrl();
    this.$store.commit('setIsPlay', false);
  },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
@import "../../assets/css/font";
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.main {
  margin-top: 20px;
  padding: 0 120px;
  display: block;
}
.bg_mask {
  position: absolute;
  top: 68px;
  left: 0;
  width: 100vw;
  min-width: 990px;
  // min-height: calc(100vh - 200px);
  max-width: 1490px;
  min-height: 560px;
  height: 100%;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.35);
  z-index: 1;
}
.bg {
  // display: block;
  position: absolute;
  top: 68px;
  left: 0;
  width: 100vw;
  height: 100vh;
  min-height: 560px;
  // min-height: calc(100vh - 200px);
  min-width: 990px;
  max-width: 1490px;
  z-index: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
  filter: blur(65px);
  opacity: 0.6;
  transform: translateZ(0);
}
.main_con {
  min-width: 750px;
  height: 100%;
  font-size: 14px;
  line-height: 1;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
  color: #fff;
  position: relative;
  overflow: hidden;
  width: 100%;
  z-index: 10;
  .info {
    display: flex;
    margin: 20px;
    border-radius: 10px;
    box-shadow: 0 0 20px 0 rgb(255 255 255 / 20%);
    padding: 5px;
    .cover_out {
      width: 100px;
      margin-right: 20px;
      .pic {
        border-radius: 10px;
        width: 100%;
        height: 100%;
      }
    }

    .title {
      vertical-align: baseline;
      display: inline-block;
      margin: 10px 0 0;
      height: 20px;
      line-height: 20px;
      font-size: 20px;
      font-weight: 600;
    }
    .artist {
      font-size: 16px;
      height: 20px;
      // color: #aaa;
      line-height: 20px;
      font-weight: 400;
      margin-top: 10px;
    }
    .song_info {
      margin-top: 10px;
      span {
        color: #ddd;
      }
      .album_name,
      .time {
        margin-right: 30px;
        color: #fff;
      }
    }
  }
  .lrc {
    overflow: auto;
    height: 360px;
    margin-top: 20px;
    margin-bottom: 20px;
    position: relative;
    // 遮罩属性和渐变函数
    -webkit-mask-image: linear-gradient(
      180deg,
      hsla(0, 0%, 100%, 0) 0,
      hsla(0, 0%, 100%, 0.6) 15%,
      #fff 25%,
      #fff 75%,
      hsla(0, 0%, 100%, 0.6) 85%,
      hsla(0, 0%, 100%, 0)
    );
    mask-image: linear-gradient(
      180deg,
      hsla(0, 0%, 100%, 0) 0,
      hsla(0, 0%, 100%, 0.6) 15%,
      #fff 25%,
      #fff 75%,
      hsla(0, 0%, 100%, 0.6) 85%,
      hsla(0, 0%, 100%, 0)
    );
    p {
      height: 40px;
      line-height: 40px;
      font-size: 16px;
      text-align: center;
      color: #ddd;
      &.active {
        animation: lrcFont 0.4s forwards;
        color: #31c27c;
      }
    }

    //隐藏滚动条
    scrollbar-width: none; /* firefox */
    -ms-overflow-style: none; /* IE 10+ */
    &::-webkit-scrollbar {
      display: none; /* Chrome Safari */
    }
  }
  .btns {
    margin: 20px 0 20px;
    .play {
      width: 130px;
      background: #31c27c;
      color: #fff;
      border: none;
      &:hover {
        color: #000;
      }
      &:active {
        box-shadow: 0 0 10px 0 rgb(65 67 70 / 30%) inset;
        border: none;
      }
    }
  }
  .list_out {
    box-shadow: 0 0 20px 0 rgb(65 67 70 / 8%);
  }
}
@keyframes lrcFont {
  from {
    font-size: 16px;
  }
  50% {
    font-size: 17px;
  }
  to {
    font-size: 18px;
  }
}

.flex_c {
  display: flex;
  align-items: center;
}
</style>



